.searchArea {
  text-align: center;
  padding: 5% 5%;
  display: grid;
  grid-row-gap: 5%;
}

.searchbar {
  text-align: center;
  font-size: 30px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  position: relative;
}

.listContainer {
  z-index: 1;
  position: absolute;
  background-color: white;
  width: 90%;
  border: 1px solid black;
}

.suggestionItem {
  border-bottom: 1px solid lightgrey;
  list-style-type: none;
  font-size: 20px;
  padding: 5px 0px;
  cursor: pointer;
}

.locbar {
  display: grid;
  grid-template-columns: 75% 24%;
  column-gap: 1%;
}

.submit {
  margin: 1% 40%;
  font-size: 15pt;
  background-color: #38b6ff;
  border-color: #1a91d6;
}

.submit:hover {
  background-color: #1a91d6;
  border-color: #1a91d6;
}